.answer .answer-header {
  width: 100%;
  height: 0.5rem;
  position: fixed;
  left: 0;
  top: 0;
  background-color: #ffffff;
  box-shadow: 0px 1px 0px 0px #eeeeee;
  z-index: 10;
}
.answer .answer-header .am-flexbox {
  height: 100%;
}
.answer .answer-header .am-flexbox .am-flexbox-item {
  text-align: center;
  line-height: 0.5rem;
  color: #333333;
  font-size: 0.16rem;
}
.answer .answer-detail {
  padding: 0.7rem 0.15rem 0.2rem;
}
.answer .answer-detail .am-list-body::before {
  display: none;
}
.answer .answer-detail .am-list-body::after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
.answer .answer-detail .answer-detail-i {
  height: 1.96rem;
  border-radius: 0.04rem;
  background-color: #ffffff;
  box-shadow: 0rem 0.08rem 0.16rem 0rem rgba(0, 0, 0, 0.1);
  margin-right: 6%;
  margin-bottom: 0.2rem;
  width: 47%;
  display: inline-block;
  position: relative;
}
.answer .answer-detail .answer-detail-i:nth-of-type(even) {
  margin-right: 0;
}
.answer .answer-detail .answer-detail-i > img {
  width: 100%;
}
.answer .answer-detail .answer-detail-i .new {
  width: 0.3rem;
  position: absolute;
  right: 0;
  top: 0;
}
.answer .answer-detail .answer-detail-i .time {
  color: #333333;
  font-size: 0.11rem;
  padding: 0 0.05rem;
  line-height: 0.2rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.answer .answer-detail .answer-detail-i .upload {
  color: #999999;
  font-size: 0.1rem;
  padding: 0 0.05rem;
  line-height: 0.2rem;
}
.answer .answer-detail .answer-detail-i .bottom {
  padding: 0 0.05rem;
  margin: 0.06rem 0 0.12rem;
  height: 0.2rem;
}
.answer .answer-detail .answer-detail-i .bottom img {
  width: 0.14rem;
  vertical-align: middle;
  margin-right: 0.04rem;
}
.answer .answer-detail .answer-detail-i .bottom span {
  line-height: 0.2rem;
  color: #dba131;
  font-size: 0.1rem;
  margin-right: 0.2rem;
}
